<template>
  <div>
    <vSearch
      @getOption="getOption"
      @searchInfo="searchInfo"
      @clearSearchInfo="clearSearchInfo"
      :pageType="pageType"
    ></vSearch>
    <el-table
      :data="tableData"
      style="width: 100%"
      max-height="675"
      :default-sort="{prop: 'date', order: 'descending'}"
    >
      <el-table-column
        prop="customerName"
        label="客户名称"
        sortable
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="customerNo"
        label="客户号"
        sortable
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="totalLoan"
        label="放款金额"
        sortable
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="contractNo"
        label="合同编号"
        sortable
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="wareHouseId"
        label="对应仓单编号(仓单总数)"
        :formatter="wwIdFormatter"
        width="220"
      >
      </el-table-column>
      <el-table-column
        label="支付回单"
        width="110"
      >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'paymentReceipt')"
            type="text"
            size="small"
          >
            查看
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        label="增值税发票"
        width="110"
      >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'invoice')"
            type="text"
            size="small"
          >
            查看
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="prev, pager, next, jumper"
        :total="totalPage"
      >
      </el-pagination>
    </div>
    <el-dialog title="支付回单" :visible.sync="paymentReceiptVisible">
      <el-tabs type="border-card">
        <el-tab-pane label="支付回单">
          <div class="recordInfo">合同编号：{{recordList.contractNo}}</div>
          <div class="recordInfo">交易客户：{{recordList.customerName}}</div>
          <div>
            <el-carousel
              height="350px"
              :autoplay="false"
              ref="payCarousel"
            >
              <el-carousel-item
                v-for="item in recordList.fileTypeZero"
                :key="item.index"
              >
                <img
                  :src="item.filePath"
                  class="bannerImg"
                  style="width: 880px; height:350px; max-width: 100%; max-height: 100%; text-align: center;"
                />
              </el-carousel-item>
            </el-carousel>
            <div class="buttonGroup">
              <el-button
                type="primary"
                @click="download(0)"
              >下载</el-button>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
      </el-dialog>
      <el-dialog title="增值税发票" :visible.sync="invoiceVisible">
        <el-tabs type="border-card">
          <el-tab-pane label="增值税发票">
            <div class="recordInfo">合同编号：{{recordList.contractNo}}</div>
          <div class="recordInfo">交易客户：{{recordList.customerName}}</div>
          <div>
            <el-carousel
              height="350px"
              :autoplay="false"
              ref="payCarousel"
            >
              <el-carousel-item
                v-for="item in recordList.fileTypeOne"
                :key="item.index"
              >
                <img
                  :src="item.filePath"
                  class="bannerImg"
                  style="width: 880px; height:350px; max-width: 100%; max-height: 100%; text-align: center;"
                />
              </el-carousel-item>
            </el-carousel>
            <div class="buttonGroup">
              <el-button
                type="primary"
                @click="download(1)"
              >下载</el-button>
            </div>
          </div>
          </el-tab-pane>
        </el-tabs>
      </el-dialog>
  </div>
</template>

<script>
import vSearch from "../components/Search.vue";
import * as ajaxUrl from "../ajax.config.js";

export default {
  data() {
    return {
      searchForm: {
        option: "",
        inputText: ""
      },
      searchParam: {},
      pageType: 5,
      paymentReceiptVisible: false,
      invoiceVisible: false,

      tableData: [],
      recordList: {
        fileTypeZero: [],
        fileTypeOne: []
      },

      currentPage: 1,
      pageSize: 1,
      totalPage: 100
    };
  },
  mounted() {
    this.searchInfo();
  },
  methods: {
    getOption(value, inputText) {
      this.searchForm.option = value;
      this.searchForm.inputText = inputText;
    },
    searchInfo() {
      this.searchParam[this.searchForm.option] = this.searchForm.inputText;
      this.axios.get(ajaxUrl.GETBILLINGRECORDLIST, {
          params: {
            pageSize: this.pageSize,
            pageNum: this.currentPage,
            search: this.searchParam
          }
      }).then(res => {
        this.tableData = res.data.resData;
        this.totalPage = res.data.page.total;
        this.pageSize = res.data.page.pageSize;
        this.currentPage = res.data.page.current;
        this.searchParam = {};
      }).catch(err => {
        console.log(err);
      });
    },
    clearSearchInfo() {
      this.searchForm.option = "";
      this.searchForm.inputText = "";
    },
    formatter(row, column) {
      return row.address;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },

    //根据类型打开dialog
    openDialog(row, type) {
      if (type == "paymentReceipt") {
        this.paymentReceiptVisible = true;
        this.recordList.customerName = row.customerName;
        this.recordList.contractNo = row.contractNo;
        for (let i = 0; i < row.contractFileList.length; i++) {
          if (row.contractFileList[i].fileType == 0) {
            this.recordList.fileTypeZero.push(row.contractFileList[i]);
          }
        }
      } else if (type == "invoice") {
        this.invoiceVisible = true;
        this.recordList.customerName = row.customerName;
        this.recordList.contractNo = row.contractNo;
        for (let i = 0; i < row.contractFileList.length; i++) {
          if (row.contractFileList[i].fileType == 1) {
            this.recordList.fileTypeOne.push(row.contractFileList[i]);
          }
        }
      }
    },
    //仓单显示格式转换
    wwIdFormatter(row, column) {
      if (row.warehouseWarrantList.length == 0) {
        return "-";
      } else if (row.warehouseWarrantList.length == 1) {
        return row.warehouseWarrantList[0].wwNo;
      } else if (row.warehouseWarrantList.length > 1) {
        return (
          row.warehouseWarrantList[0].wwNo +
          "~" +
          row.warehouseWarrantList[row.warehouseWarrantList.length - 1].wwNo +
          "(" +
          row.warehouseWarrantList.length +
          ")"
        );
      }
    },
    download(fileType) {
      let url = ajaxUrl.DOWNLOADZIP + "?contractId=" + this.recordList.contractNo + "&fileType=" + fileType
      this.axios({
        method: 'get',
        url: url,
        responseType:'blob',
      }).then((data) => {
        if(!data) {
          return;
        }
        let url = window.URL.createObjectURL(data.data)
        let link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.setAttribute('download', 'image.zip')
        document.body.appendChild(link)
        link.click()
      }).catch(err => {
        console.log(err);
      });
    },
  },
  components: {
    vSearch
  }
};
</script>

<style>
.pagination {
  text-align: center;
}
.buttonGroup {
  text-align: center;
  margin-left: -50px; 
}
</style>



